<template>
    <div class="mine"> 
        <div class="matchitems">
            <div class="title">我的交換</div>
            <div class="container">
                <div class="own">
                    <div class="own-title">我發起的</div>
                     <exchangeItem :itemList = "myExchangeList"></exchangeItem>
                </div>
                <div class="join">
                    <div class="join-title">我參與的</div>
                    <exchangeItem :itemList = "myApplyList"></exchangeItem>
                </div>
            </div>
        </div>
        <div class="togetheritems">
            <div class="title">我的揪團</div>
            <div class="container">
                <div class="own">我發起的揪團</div>
                <div class="other">我參與的揪團</div>
            </div>
        </div>
        <div class="followingitems"></div>
    </div>
</template>

<script>
import { getMyExchangeItem } from '../../api/user'; 
import exchangeItem from '../Exchange/components/exchangeItem.vue'
export default {
    components:{
        exchangeItem
    },
    data(){
        return {
            myApplyList: [],
            myExchangeList: [],
        }
    },  
    mounted(){
        this.getUserItemList()
    },  
    methods:{
        getUserItemList(){
            let params = {
                userName:this.$store.state.user.userName,
                type:'exchange'
            }
            getMyExchangeItem(params).then(res => {
                console.log(res);
                this.myApplyList = res.body.myApplyList
                this.myExchangeList = res.body.myExchangeList
                }).catch(err => {
                    console.log(err);
                });
        }
    }
}
</script>

